<script setup lang="ts">
import { usePageTitle } from '@/composables/';
import { useI18n } from 'vue-i18n';
import { computed, onBeforeUnmount, onMounted, watchEffect } from 'vue';
import gsap from 'gsap';
import { lgAndSmaller, largerThanSLg } from '@/utils';

const { t } = useI18n();
usePageTitle(t('Home Page'));
const appName = computed(() => import.meta.env.VITE_APP_NAME);

function showSidebar() {
  gsap.to('#main-body', {
    gridTemplateColumns: '220px 1fr',
  });
}

function clearProps() {
  gsap.to('#main-body', {
    clearProps: 'gridTemplateColumns',
  });
}

watchEffect(() => {
  if (lgAndSmaller.value) {
    showSidebar();
  }

  if (largerThanSLg.value) {
    clearProps();
  }
});

onMounted(() => {
  if (largerThanSLg.value) return;

  showSidebar();
});

onBeforeUnmount(() => {
  clearProps();
});
</script>

